import React, { useEffect, useState } from 'react'
import { Button, Input, message, Select, Spin } from 'antd'
import { connect } from 'react-redux'
import { getDataAction, handleInteriorDesignAction, sendNotificationAction, updateUserAction } from '../../redux/dataDuck'
import text from '../text'
import { Link } from 'react-router-dom'
import ReactImageMagnify from 'react-image-magnify'
import ConfirmationModal from '../common/ConfirmationModal'
import { handleConfirmationBoxAction } from '../../redux/appDuck'
let { Option } = Select

const InteriorDesign = ({sendNotification, info, dataUpdated, history, fetching, language, getData, updateUser, handleInfo, toggleModal}) => {

    let [option, setOption] = useState('kitchenCabinet')
    let [background, setBackground] = useState('/img/intDesign/countertops/GraphiteCemento.jpg')

    let handleSubmit = () => {
        if(!info.kitchenCabinet || !info.closetCabinet) return message.error(text.interiorDesign.missingSelection[language])
        // if(!info.floor || !info.showerTile) return message.error(text.interiorDesign.missingSelection[language])
        toggleModal()
    }

    let confirmAction = async () => {
        sendNotification('intDesign')
        await updateUser()
        toggleModal()
        history.push('/propietary-service')
    }

    let handleOption = opt => {
        switch(opt){
            case 'kitchenCabinet':
                setOption(opt)
                if(info.kitchenCabinet) handleBackground(info.kitchenCabinet, opt)
                else setBackground('/img/intDesign/countertops/GraphiteCemento.jpg')
                return 
            case 'closetCabinet':
                setOption(opt)
                if(info.closetCabinet) handleBackground(info.closetCabinet, opt)
                else setBackground('/img/intDesign/cabinets/aniz.jpg')
                return 
            // case 'floor':
            //     if(info.floor) handleBackground(info.floor)
            //     else setBackground('/img/intDesign/countertops/GraphiteCemento.jpg')
            //     return setOption(opt)
            // case 'showerTile':
            //     if(info.kitchenCabinet) handleBackground(info.kitchenCabinet)
            //     else setBackground('/img/intDesign/demo/tileExtra.webp')
            //     return setOption(opt)
            default:
                return
        }
    }

    let handleBackground = (value, opt) => {
        if(opt) option = opt
        if(option === 'closetCabinet'){
            switch(value){
                case 'Aniz':
                    handleInfo(option, value)
                    return setBackground('/img/intDesign/cabinets/aniz.jpg')
                case 'Avellana':
                    handleInfo(option, value)
                    return setBackground('/img/intDesign/cabinets/avellana.jpg')
                case 'Nogal':
                    handleInfo(option, value)
                    return setBackground('/img/intDesign/cabinets/nogal.jpg')
                case 'Paja':
                    handleInfo(option, value)
                    return setBackground('/img/intDesign/cabinets/paja.jpg')
                case 'Rattan':
                    handleInfo(option, value)
                    return setBackground('/img/intDesign/cabinets/rattan.jpg')
                default: 
                    return
            }
        }
        if(option === 'kitchenCabinet'){
            switch(value){
                case 'Graphite Cemento':
                    handleInfo(option, value)
                    return setBackground('/img/intDesign/countertops/GraphiteCemento.jpg')
                case 'Limestone':
                    handleInfo(option, value)
                    return setBackground('/img/intDesign/countertops/Limestone.jpg')
                case 'Ornamental':
                    handleInfo(option, value)
                    return setBackground('/img/intDesign/countertops/Ornamental.jpg')
                case 'Santta Cecilia':
                    handleInfo(option, value)
                    return setBackground('/img/intDesign/countertops/SantaCecilia.jpg')
                case 'Sand White':
                    handleInfo(option, value)
                    return setBackground('/img/intDesign/countertops/Sand White.jpg')
                case 'Snow White':
                    handleInfo(option, value)
                    return setBackground('/img/intDesign/countertops/Snow White.jpg')
                // case 'Aspen Beige':
                //     handleInfo(option, value)
                //     return setBackground('/img/intDesign/demo/floorDemoA.webp')
                // case 'Nevada Gris':
                //     handleInfo(option, value)
                //     return setBackground('/img/intDesign/demo/floorDemoB.webp')
                // case 'Emblem Grey':
                //     handleInfo(option, value)
                //     return setBackground('/img/intDesign/demo/floorDemoC.webp')
                // case 'Emblem Brown':
                //     handleInfo(option, value)
                //     return setBackground('/img/intDesign/demo/floorDemoD.webp')
                // case 'Khalos':
                //     handleInfo(option, value)
                //     return setBackground('/img/intDesign/demo/tileExtraDemo.webp')
                // case 'sameAsFloor':
                //     if(info.floor) {
                //         handleInfo(option, info.floor)
                //         return handleBackground(info.floor)
                //     }
                //     return message.error(text.interiorDesign.missingFloorSelect[language])
                default: 
                    return
            }
        }
    }

    useEffect(() => {
        getData()
    }, [])

    return (
        <div className='application-container'>
             {
                fetching ? 
                    <div className='spinner-full-page'><Spin size='large'/></div>
                    :
                    <div className="application-content no-justify">
                        <div>
                            <div className="app-top-icon">
                                <img src="/img/sidebar/intDesign.svg" alt="profile"/>
                                <p>{text.sidebar.interiorDesign[language]}</p>
                            </div>
                        <h2>{text.sidebarSubtitle.interiorDesign[language]}</h2>
                        </div>
                        <div className='padded-app-container form-container'>
                            <div className="multiple-inputs map-inputs int-design-input">
                                <Input disabled value={info.kitchenCabinet ? `${text.interiorDesign.kitchen[language]}: ${info.kitchenCabinet}` : ` ${text.interiorDesign.kitchen[language]}:`}/>
                                <Input disabled value={info.closetCabinet ? `${text.interiorDesign.closet[language]}: ${info.closetCabinet}` :  ` ${text.interiorDesign.closet[language]}:`}/>
                                {/* <Input disabled value={info.floor ? `${text.interiorDesign.floors[language]}: ${info.floor}` :  ` ${text.interiorDesign.floors[language]}:`}/>
                                <Input disabled value={info.showerTile ? `${text.interiorDesign.showerTile[language]}: ${info.showerTile}` :  ` ${text.interiorDesign.showerTile[language]}:`}/> */}
                            </div>
                        </div>
                        <div className='padded-app-container form-container'>
                            <div className="int-design-container">
                                <div className="int-design-left">
                                    <ReactImageMagnify
                                    {...{
                                        smallImage: {
                                            alt: 'Demo',
                                            isFluidWidth: true,
                                            src: background
                                        },
                                        largeImage: {
                                            src: background,
                                            width: 1200,
                                            height: 1800
                                        },
                                        enlargedImageContainerDimensions: {
                                            width: 300,
                                            height: '100%'
                                        }
                                    }} 
                                    />
                                </div>
                                <div className="int-design-right">
                                    <div className="int-design-select">
                                        <p>{text.interiorDesign.area[language]}: </p>
                                        <Select
                                            defaultValue='kitchenCabinet'
                                            onChange={handleOption}
                                        >
                                            <Option value='kitchenCabinet'>{text.interiorDesign.kitchen[language]}</Option>
                                            <Option value='closetCabinet'>{text.interiorDesign.closet[language]}</Option>
                                            {/* <Option value='floor'>{text.interiorDesign.floors[language]}</Option>
                                            <Option value='showerTile'>{text.interiorDesign.showerTile[language]}</Option> */}
                                        </Select>
                                    </div>
                                    {
                                        option === 'kitchenCabinet' ? 
                                            <div className="sample-container">
                                                <img className={info.kitchenCabinet === 'Graphite Cemento' ? 'active-sample' : ''} onClick={() => handleBackground('Graphite Cemento')} src="/img/intDesign/countertops/GraphiteCemento.jpg" alt="kitchenCabinet"/>
                                                <img className={info.kitchenCabinet === 'Limestone' ? 'active-sample' : ''} onClick={() => handleBackground('Limestone')} src="/img/intDesign/countertops/Limestone.jpg" alt="kitchenCabinet"/>
                                                <img className={info.kitchenCabinet === 'Ornamental' ? 'active-sample' : ''} onClick={() => handleBackground('Ornamental')} src="/img/intDesign/countertops/Ornamental.jpg" alt="kitchenCabinet"/>
                                                <img className={info.kitchenCabinet === 'Santa Cecilia' ? 'active-sample' : ''} onClick={() => handleBackground('Santta Cecilia')} src="/img/intDesign/countertops/SantaCecilia.jpg" alt="kitchenCabinet"/>
                                                <img className={info.kitchenCabinet === 'Sand White' ? 'active-sample' : ''} onClick={() => handleBackground('Sand White')} src="/img/intDesign/countertops/Sand White.jpg" alt="kitchenCabinet"/>
                                                <img className={info.kitchenCabinet === 'Snow White' ? 'active-sample' : ''} onClick={() => handleBackground('Snow White')} src="/img/intDesign/countertops/Snow White.jpg" alt="kitchenCabinet"/>
                                            </div>
                                        :
                                        option === 'closetCabinet' ? 
                                            <div className="sample-container">
                                                <img className={info.closetCabinet === 'Aniz' ? 'active-sample' : ''} onClick={() => handleBackground('Aniz')} src="/img/intDesign/cabinets/aniz.jpg" alt="closetCabinet"/>
                                                <img className={info.closetCabinet === 'Avellana' ? 'active-sample' : ''} onClick={() => handleBackground('Avellana')} src="/img/intDesign/cabinets/avellana.jpg" alt="closetCabinet"/>
                                                <img className={info.closetCabinet === 'Nogal' ? 'active-sample' : ''} onClick={() => handleBackground('Nogal')} src="/img/intDesign/cabinets/nogal.jpg" alt="closetCabinet"/>
                                                <img className={info.closetCabinet === 'Paja' ? 'active-sample' : ''} onClick={() => handleBackground('Paja')} src="/img/intDesign/cabinets/paja.jpg" alt="closetCabinet"/>
                                                <img className={info.closetCabinet === 'Rattan' ? 'active-sample' : ''} onClick={() => handleBackground('Rattan')} src="/img/intDesign/cabinets/rattan.jpg" alt="closetCabinet"/>
                                            </div>
                                        // :
                                        // option === 'floor' ? 
                                        //     <div className="sample-container">
                                        //         <img className={info.floor === 'Aspen Beige' ? 'active-sample' : ''} onClick={() => handleBackground('Aspen Beige')} src="/img/intDesign/demo/floorA.webp" alt="floor"/>
                                        //         <img className={info.floor === 'Nevada Gris' ? 'active-sample' : ''} onClick={() => handleBackground('Nevada Gris')} src="/img/intDesign/demo/floorB.webp" alt="floor"/>
                                        //         <img className={info.floor === 'Emblem Grey' ? 'active-sample' : ''} onClick={() => handleBackground('Emblem Grey')} src="/img/intDesign/demo/floorC.webp" alt="floor"/>
                                        //         <img className={info.floor === 'Emblem Brown' ? 'active-sample' : ''} onClick={() => handleBackground('Emblem Brown')} src="/img/intDesign/demo/floorD.webp" alt="floor"/>
                                        //     </div>
                                        // :
                                        // option === 'showerTile' ? 
                                        //     <div className="sample-container sample-with-text">
                                        //         <p className={info.showerTile && info.showerTile !== 'Khalos' ? 'sample-with-text-active' : ''} onClick={() => handleBackground('sameAsFloor')}>{text.interiorDesign.sameTile[language]}</p>
                                        //         <img className={info.showerTile === 'Khalos' ? 'active-sample' : ''} onClick={() => handleBackground('Khalos')} src="/img/intDesign/demo/tileExtra.webp" alt="floor"/>
                                        //     </div>
                                        :
                                            null
                                    }
                                </div>
                            </div>
                        </div>
                        <div className='multiple-buttons intd-buttons'>
                            <Link to='/buy'><Button loading={false} className='nav-button'>{text.button.returnCaps[language]}</Button></Link>
                            <Button onClick={handleSubmit} loading={false} className='nav-button'>{text.button.continue[language]}</Button>
                        </div>
                    </div>
            }
            <ConfirmationModal confirmAction={confirmAction}>
                <div className="modal-container-confirm">
                    <p>{text.interiorDesign.kitchen[language]}: <span>{info.kitchenCabinet}</span></p>
                    <p>{text.interiorDesign.closet[language]}: <span>{info.closetCabinet}</span></p>
                    <p>{text.interiorDesign.floors[language]}: <span>{info.floor}</span></p>
                    <p>{text.interiorDesign.showerTile[language]}: <span>{info.showerTile}</span></p>
                    <h3>{text.interiorDesign.warning[language]}</h3>
                </div>
            </ConfirmationModal>
        </div>
    )
}

let mapState = ({
    app: {language},
    user: {fetching},
    data: {info, dataUpdated}
}) => ({
    language,
    fetching,
    info,
    dataUpdated
})

export default connect(
    mapState,
    {
        getData: getDataAction,
        updateUser: updateUserAction,
        handleInfo: handleInteriorDesignAction,
        toggleModal: handleConfirmationBoxAction,
        sendNotification: sendNotificationAction
    }
)(InteriorDesign)
